import React, { Component } from "react";
import "./Refer.css";
import refer from "../../public/images/refer.png";
import thinking from "../../public/images/thinking.png";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { Typography, Button, useMediaQuery } from "@material-ui/core";
import Footer from "../Footer/Footer";
import TextField from "@material-ui/core/TextField";
import {
  EmailShareButton,
  FacebookShareButton,
  WhatsappShareButton,
  EmailIcon,
  FacebookIcon,
  WhatsappIcon,
} from "react-share";
import CustomLoader from "../Loader";

const GREEN = "#06bfad";
const PINK = "#e03268";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  backButton: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  iconContainer: {
    // define styles for icon container
    transform: "scale(1.8)",
    [theme.breakpoints.down("sm")]: {
      transform: "scale(1.4)",
    },
    [theme.breakpoints.down("xs")]: {
      transform: "scale(1)",
    },
  },
  steps: {
    "& .MuiStepIcon-root.MuiStepIcon-active": {
      color: PINK,
      fontWeight: "bold",
      transition: "all 0.5s ease-in-out",
      content: "none",
      "&:hover": {
        color: GREEN,
      },
    },
  },
}));

function Steps() {
  const classes = useStyles();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down("xs"));
  return (
    <Stepper
      className={classes.steps}
      alternativeLabel={!matches}
      orientation={matches ? "vertical" : "horizontal"}
    >
      {[
        "Invite Friends & Family by sharing your unique refferal code",
        "They signup and enroll to our Courses",
        "You start getting rewards",
      ].map((label, i) => (
        <Step key={i} active={true}>
          <StepLabel
            classes={{
              iconContainer: classes.iconContainer,
            }}
          >
            <div>
              <Typography variant="h6">{label}</Typography>
            </div>
          </StepLabel>
        </Step>
      ))}
    </Stepper>
  );
}

class ReferComponent extends Component {
  constructor(props) {
    super(props);
    this.copy = this.copy.bind(this);
    this.state = {
      title: "Invite to Join Robofreaks",
      text: `Robofreaks is best platform to learn robotics and coding. \n
            I highly recommend you take the courses of Robofeaks for your child too. \n
            signup using my invite link https://robofreaks.herokuapp.com/referral/${
              this.props.profile ? this.props.profile.referral_code : null
            }`,
      description: `Robofreaks is best platform to learn robotics and coding. \nI highly recommend you take the courses of Robofeaks for your child too. \nsignup using my invite link`,
    };
  }
  copy() {
    var textField = document.createElement("textarea");
    textField.innerText = this.state.text;
    document.body.appendChild(textField);
    textField.select();
    document.execCommand("copy");
    this.props.setMsg("copied");
    // alert("copied");
    textField.remove();
  }

  render() {
    return (
      <>
        <div className="container-fluid refer">
          <div className="row justify-content-center">
            <div className="heading offset-lg-1 col-12 col-md-5 align-self-center">
              <h1 style={{ color: "#06bfad" }}>
                Refer and get <br /> Rewarded
              </h1>
              <div className="col-12 col-lg-6">
                <p style={{ marginTop: 20, fontSize: 20 }}>
                  Invite your friends to join robofreaks and enroll to our
                  courses and win exciting rewards
                </p>
              </div>
            </div>
            <div className="col-12 col-md-6">
              <img
                alt="reward"
                src={refer}
                width="100%"
                height="100%"
                className="rewardImg"
              />
            </div>
          </div>
          <div className="container">
            <div className="row justify-content-center content">
              <div className="col-12" style={{ textAlign: "center" }}>
                <h1>
                  <strong>What you have to do?</strong>
                  <img
                    alt="What you have to do?"
                    src={thinking}
                    height="100px"
                    width="100px"
                  />
                </h1>
              </div>
              <div className="col-12">
                <div style={{ overflowX: "auto" }}>
                  <Steps />
                </div>
              </div>
            </div>
            <div className="row justify-content-center content">
              <div className="col-8 col-md-10">
                <TextField
                  fullWidth
                  // id="myInput"
                  value={
                    this.props.profile
                      ? `https://robofreaks.herokuapp.com/referral/${this.props.profile.referral_code}`
                      : ""
                  }
                  InputProps={{
                    readOnly: true,
                  }}
                  variant="outlined"
                />
              </div>
              <div className="col-4 col-md-2">
                <Button
                  onClick={this.copy}
                  style={{
                    height: "100%",
                    width: "100%",
                    backgroundColor: "#e03268",
                    color: "white",
                    outline: "none",
                  }}
                  variant="contained"
                >
                  Copy Link
                </Button>
              </div>
              <div className="col-12">
                <div className="row justify-content-center">
                  <div style={{ margin: 5 }}>
                    <EmailShareButton
                      style={{ outline: "none" }}
                      url={`https://robofreaks.herokuapp.com/referral/${this.props.profile.referral_code}`}
                      subject={this.state.title}
                      body={this.state.text}
                    >
                      <EmailIcon round size={40} />
                    </EmailShareButton>
                  </div>
                  <div style={{ margin: 5 }}>
                    <FacebookShareButton
                      style={{ outline: "none" }}
                      url={`https://robofreaks.herokuapp.com/referral/${this.props.profile.referral_code}`}
                      quote={this.state.description}
                    >
                      <FacebookIcon round size={40} />
                    </FacebookShareButton>
                  </div>
                  <div style={{ margin: 5 }}>
                    <WhatsappShareButton
                      style={{ outline: "none" }}
                      url={`https://robofreaks.herokuapp.com/referral/${this.props.profile.referral_code}`}
                      title={this.state.description}
                    >
                      <WhatsappIcon round size={40} />
                    </WhatsappShareButton>
                  </div>
                </div>
              </div>
            </div>
            <div
              className="row justify-content-center content"
              style={{
                fontSize: 20,
                color: "#e03268",
                textAlign: "center",
                marginBottom: 20,
              }}
            >
              You’ve got {this.props.myReferrals.length} referrals so far. Get
              Started now!
            </div>
            {/* <div
              className="row justify-content-center content"
              style={{ marginBottom: 20 }}
            >
              <div className="col-12" style={{ textAlign: "center" }}>
                <h1>
                  <strong>People you have referred</strong>
                </h1>
              </div>
              <div className="col-12"></div>
            </div> */}
          </div>
        </div>
        <Footer />
      </>
    );
  }
}

function Refer(props) {
  if (
    !(
      Object.keys(props.profile).length === 0 &&
      props.profile.constructor === Object
    )
  ) {
    return (
      <ReferComponent
        profile={props.profile}
        myReferrals={props.myReferrals}
        setMsg={props.setMsg}
      />
    );
  } else {
    return (
      <div className="loader">
        <CustomLoader />
      </div>
    );
  }
}

export default Refer;
